<template>
    <div class="content">
      <el-form ref="form" :model="formData" label-width="80px">
        <el-form-item label="姓名">
          <el-input v-model="formData.username"></el-input>
        </el-form-item>
        <el-form-item label="用户名">
          <el-input v-model="formData.user"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input type="password" v-model="formData.pwd"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">添加</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
    export default {
        name: "register",
      data(){
          return{
            formData:{
              user: '',
              username: '',
              pwd: ''
            }
          }
      },
      methods:{
          submit(){
            this.$axios.post('register',this.formData).then((res)=>{
              if(res.code==200){
                this.$message({
                  type: 'success',
                  message: '添加成功!'
                });
              }else{
                this.$message({
                  type: 'warning',
                  message: res.msg
                });
              }
            })
          }
      }
    }
</script>

<style scoped>

</style>



